// 
// Variables
// ------------------------------------------------

::-webkit-scrollbar {
  width: 12px;
  background-color: #F5F5F5;
}

::-webkit-scrollbar-thumb {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
  background-color: #555;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
  background-color: #F5F5F5;
}

$gray-700: #212529 !default;
$gray-800:  #3A3A3A !default;
$primary: #007bff !default;
$secondary: #5f5b56 !default;
$danger:  #c7254e !default;
$light: #f8f9fa !default;
$active: #00d88a !default;
$dark: #37526a !default;
$white: #fff !default;
$font-family: 'Lato', sans-serif !default;
$font-size-sm: .875rem !default;
$font-weight-normal:  400 !default;
$font-weight-medium:  500 !default;
$font-weight-bold:  700 !default;
$border-light: rgba($light, .15) !default;
$border-gray: rgba(211, 211, 211, .63) !default;