// 
// Custom Styles
// ------------------------------------------------

a:not(.btn) {
  text-decoration: none !important;
  transition: color .25s ease-in-out, background-color .25s ease-in-out;
}

.badge-lg {
  padding: .4em .8em;
  font-size: .8125rem;
}

.nav {
  &.flex-column {
    .nav-link {
      padding: 0;
      margin-bottom: .375rem;
    }
  }

  &.nav-light {
    .nav-link {
      color: rgba($white, .75);
      &:hover {
        color: $light;
      }
    }
  }
}

.btn-check {
  position: absolute;
  clip: rect(0, 0, 0, 0);
  pointer-events: none;
}

.badge-icon {
  display: inline-block;
  width: 1.6875rem;
  height: 1.6875rem;
  border-radius: 50%;
  font-size: 1rem;
  line-height: 1.25rem;
}

.ratings {
  display: inline-flex;
  font-size: .8125rem;
  i.active {
    color: #d0a620;
  }
}

.hover-visible-content {
  opacity: 0;
  visibility: hidden;
  .hover-visible:hover & {
    opacity: 1;
    visibility: visible;
  }
}

.nav-tabs,
.nav-pills {
  .nav-link {
    color: lighten($gray-700,15%);
    &:hover:not(.active) {
      color: darken($gray-700,7.5%);
    }
  }
}

.nav-tabs {
  .nav-link.active {
    color: $primary;
  }
}

.nav-pills-light {
  .nav-link {
    color: $white;
    &:hover,
    &:focus {
      color: rgba($white, .9) !important;
    }
    &.active {
      color: $gray-800 !important;
      background-color: $white;
    }
  }
}

.nav-pills-dark {
  .nav-link {
    font-size: $font-size-sm;
    padding: .375rem .875rem;
  }
  .nav-link.active {
    background-color: $gray-800;
  }
}

.card-bordered {
  border-top: 6px solid $secondary;
}

.card-title {
  font-size: 1.0625rem;
  font-weight: $font-weight-bold;
}

.btn-wide {
  width: 6rem;
}

.link-heading,
.card-title {
  a {
    color: $gray-700;
    &:hover {
      color: $primary;
    }
  }
}

.card-text {
  font-size: $font-size-sm
}

.card-header,
.card-footer {
  background-color: transparent
}

.card-footer {
  display: flex;
  justify-content: space-between
}

.card-hover {
  transition: background-color .25s ease-in-out, box-shadow .3s ease-in-out;
  &:hover {
    box-shadow: 0 .1875rem .375rem rgba(0,0,0, .08) !important;
  }
}

.card-overlay {
  position: relative;

  .overlay-hidden,
  .overlay-visible {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 4
  }

  .overlay-hidden {
    width: 100%;
    height: 100%;
    opacity: 0;
    background-color: rgba($secondary, .55);
    transition: opacity .3s ease-in-out
  }

  &:hover {
    .overlay-hidden {
      opacity: 1
    }
  }
}

.bg-cover {
  background-size: cover;
  background-repeat: no-repeat
}

.bg-positon-lc {
  background-position: left center
}

.text-warning-alt {
  color: #d0a620
}

.text-success-alt {
  color: #8ba753;
}

.bg-success-alt {
  background-color: #8ba753;
}

.form-control,
select.custom-select {
  &:focus {
    box-shadow: none;
  }
}

.shadow {
  box-shadow: 0 .375rem 1rem -.0625rem rgba(0,0,0, .1) !important;
}

.badge-notification {
  position: absolute;
  top: 0;
  right: 0;
  margin: {
    top: -0.125rem;
    right: -0.125rem;
  }
  width: 1.25rem;
  height: 1.25rem;
  line-height: .9375rem;
}

.avatar-bordered {
  border: 4px solid $white;
  box-shadow: 0 0 1.5rem rgba($white, .45);
}

@keyframes slide-down {
  from {
    opacity: 0;
    transform: translateY(.5rem);
  }
  to {
    opacity: 1;
    transform: none;
  }
}

.navbar-dark {
  .navbar-nav .nav-link,
  .avatar-link {
    color: $white;

    &.dropdown-toggle {
      position: relative;

      &::after {
        color: lighten($gray-800, 40%);
      }

      &::before {
        display: block;
        position: absolute;
        top: 100%;
        width: 100%;
        height: .25rem;
        content: '';
      }

      &.no-caret {
        &::after {
          display: none;
        }
      }
    }

    &:hover {
      color: rgba($white, .9);
    }
  }

  .dropdown-hover {
    .dropdown-menu {
      display: none;
      min-width: 13rem;
    }
    &:hover .dropdown-menu {
      display: block;
      animation: slide-down .2s ease-in-out;
    }
  }

  .dropdown-menu {
    position: relative;
    font-size: $font-size-sm;
    background-color: darken($gray-800, 5%);
    border: 2px solid #f9d39a;
    border-radius: .625rem;
    &::before {
      content: '';
      position: absolute;
      border-style: solid;
      border-width: 0 .5rem .625rem;
      border-color: #f9d39a transparent;
      display: block;
      width: 0;
      z-index: 1;
      top: -.625rem;
      left: 1.25rem;
    }
    &.dropdown-menu-right {
      &::before {
        left: auto;
        right: 1.25rem;
      }
    }
    .dropdown-item {
      color: #efc17c;
      background-color: transparent !important;
      padding: {
        top: .625rem;
        bottom: .625rem;
      }
      &:hover {
        color: $white;
      }
    }
  }

  .no-caret {
    + .dropdown-menu-right {
      margin-right: -.875rem;
      width: 17rem;

      .dropdown-item {
        white-space: normal;
      }
    }
  }

  .dropdown-menu-avatar {
    min-width: 15rem;
    position: absolute;

    .dropdown-item {
      white-space: normal;
    }
  }
}

@media (min-width: 768px) {
  .w-md-auto {
    width: auto !important;
  }
}

.border-top-primary {
  border-top-color: $primary;
}

.avatar {
  position: relative;
  display: inline-block;
  width: 3.125rem;
  height: 3.125rem;
  border-radius: 50%;

  .avatar-img {
    object-fit: cover;
    object-position: center;
    width: 100%;
    border-radius: 50%;
  }

  .avatar-status {
    display: inline-block;
    width: .6875rem;
    height: .6875rem;
    border-radius: 50%;
    position: absolute;
    right: .125rem;
    bottom: .125rem;
    background-color: lighten($secondary, 15%);
    &.active {
      background-color: $active;
    }
  }
}

.btn,
.form-control {
  &.disabled,
  &[disabled] {
    pointer-events: none;
    cursor: not-allowed;
  }
}

.bg-faded-primary {
  background-color: rgba($primary, .14);
}

.badge-faded-danger {
  color: $danger;
  background-color: rgba($danger, .12);
}

.icon {
  display: inline-block;
  width: 2rem;
  height: 2rem;
  color: $secondary;
  border: .0625rem solid $secondary;
  border-radius: 50%;
  text-align: center;
  line-height: 1.75rem;

  &.icon-sm {
    width: 1.5rem;
    height: 1.5rem;
    font-size: inherit;
    line-height: 1.375rem;
  }
}

.select2-container .select2-selection {
  @extend .form-control;
  padding-left: .25rem;
}

.select2-container .select2-selection__arrow {
  top: 50%;
  margin-top: .25rem;
}

.select2-container--default .select2-selection--multiple {
  .select2-selection__choice {
    margin-top: 0;
  }

  .select2-search--inline .select2-search__field {
    margin-top: 0;
    margin-bottom: 0.25rem;
  }
}

.quill-editor #quill-editor {
  min-height: 22.5rem;
}

.filepond {
  background-color: #f5f5f5;
  border: .125rem dashed #e5e5e5;
  border-radius: .5rem;
}

.filepond--panel .filepond--panel-root {
  background-color: transparent !important;
}

.filepond--credits {
  display: none;
}

.rounded-xl {
  border-radius: .5rem;

  .card-img-top {
    border-top-left-radius: .5rem;
    border-top-right-radius: .5rem;
  }
}

.rounded-left {
  border-top-left-radius: .25rem;
  border-bottom-left-radius: .25rem;
}

.font-size-lg {
  font-size:1.125rem
}

.font-size-md {
  font-size:.9375rem
}

.font-size-sm {
  font-size:.875rem
}

.font-size-ms {
  font-size:.8125rem
}

.font-size-xs {
  font-size:.75rem
}

.font-size-xxs {
  font-size:.625rem
}

.font-weight-nomral {
  font-weight:$font-weight-normal
}

.font-weight-md {
  font-weight:$font-weight-medium
}

.font-weight-bold {
  font-weight:$font-weight-bold
}

.border-gray {
  border-color: $border-gray !important
}

.text-white-75 {
  color: rgba($white, .75)
}

.bg-gray-100 {
  background-color: #fafafa
}

.bg-gray-800 {
  background-color: $gray-800
}

a.bg-gray-800:hover {
  background-color: darken($gray-800,10%)
}

.op-75 {
  opacity: .75
}